input, select, textarea {
  border-radius: 5px;
}

* {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
button {
  border-radius: 5px;
  padding: 0.65rem 0.9rem;
  font-weight: 600;
  border: 1px solid transparent;
  cursor: pointer;
  transition: background-color 0.2s ease, box-shadow 0.2s ease, transform 0.08s ease-in-out;
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(165,210,133,0.25);
}

button:active {
  transform: scale(0.99);
}
html, body {
  height: 100%;
  margin: 0;
  background: radial-gradient(70% 90% at 80% 10%, rgba(0,255,136,0.06) 0%, rgba(0,255,136,0) 60%),
              linear-gradient(135deg, var(--bg-gradient-start), var(--bg-gradient-end));
  background-attachment: fixed;
  color: var(--text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  min-height: 100vh;
  background: inherit;
}
:root {
  --green-accent: #00ff88;
  --green-accent-hover: #33ffaa;
  --green-muted: #00ff88;
  --green-muted-hover: #33ffaa;
  --text-primary: #f1f5f9;
  --text-muted: #c9ffda;
  --bg-gradient-start: rgba(35,45,60,1);
  --bg-gradient-end: rgba(50,80,70,0.95);
  --card-bg: rgba(35, 45, 60, 0.92);
  --card-bg-alt: rgba(50, 80, 70, 0.82);
  --border-soft: rgba(255,255,255,0.18);
  --overlay: rgba(0,0,0,0.6);
}
